export function updUserfromFb(v,id) {  
  const _state = _State().common; 
  var us_ = _state.activeUserList;
  if(us_){    
    var s = us_[id];   
    const dd = JSON.stringify(s);
    var kt =JSON.parse(dd);
    Object.keys(kt).map(flk=>{
        if(v[flk]!==undefined && v[flk]!==null && kt[flk]!==v[flk]){
          kt[flk]= v[flk]
        }
    })
    us_[id] = kt;
    //_Dispatch && _Dispatch(UpdKeyValue({key:'activeUserList',value:us_}));
    _Dispatch && _Dispatch(_formOberves());
    
  }
}


export function tokenisValid() {
  return function (dispatch, getState) {
    var doc = {id:'dgt047j3',limit:1000,page:1,sortBy:'date.desc'};
    const query= ` 
    query($doc: FindbyIdCda!){
      payload:  getCdasbyId(cda: $doc) {
        isValidToken,
        isTesting,
        liteMode,
        hyperMode
      }
    }  
    `;
    let variables={doc};
    _Util.fetchGraphQL(GRAPHQLURL,{query,variables}) 
    .then(res => {
      const state = getState().common;
      if(res && res.data && res.data.payload){ 
        if(state.isValidToken!==res.data.payload.liteMode){                          
          dispatch(UpdKeyValue({key:'liteMode',value:res.data.payload.liteMode}));
        }
        if(state.isValidToken!==res.data.payload.isValidToken){                          
          dispatch(UpdKeyValue({key:'isValidToken',value:res.data.payload.isValidToken}));
        }
        if(state.hyperMode!==res.data.payload.hyperMode){
          dispatch(UpdKeyValue({key:'hyperMode',value:res.data.payload.hyperMode}));
        }
        if(state.hyperMode!==res.data.payload.isTesting){
          dispatch(UpdKeyValue({key:'isTesting',value:res.data.payload.isTesting}));
        }        
      }else{
        if(state.isValidToken!==false){
          dispatch(UpdKeyValue({key:'isValidToken',value:false}));
        }        
      }      
    }).catch(error => {      
      console.log(error); //eslint-disable-line
    });
  }
}











